<template>
  <div>
    <nav class="navbar navbar-expand-md fixed-top navbar-dark bg-dark"  ng-controller="navpart">
      <a class="navbar-brand" href="index.html">MAK JS</a>
      <button class="navbar-toggler p-0 border-0" type="button" data-toggle="offcanvas">
        <span class="navbar-toggler-icon"></span>
      </button>

      <div class="navbar-collapse offcanvas-collapse" id="navbarsExampleDefault">
        <ul class="navbar-nav mr-auto">
          <li v-for="item,index in header_title" :class="now==index?'nav-item active':'nav-item'">
            <a href="javascript:;" class="nav-link" @click="fnTabClick(index)">{{item}}</a>
          </li>
        </ul>
      </div>
    </nav>
      <jsInfo v-if="now==0"></jsInfo>
      <exam v-if="now==1"></exam>
      <p class="h6 text-muted" style="text-align:center;">持续更新中...</p>
      <div class="container">
        <hr class="featurette-divider">
        <p class="blockquote-footer">Make by @becky</a>.</p>
      </div>
    <p class="float-right"><a href="#">TOP</a></p>
  </div>
</template>

<script>
import jsInfo from '@/components/js-info'
import exam from '@/components/exam'
export default {
  name: 'index',
  data () {
    return {
      now: 0,
      header_title: ['JavaScript框架','DEMO']
    }
  },
  methods: {
    fnTabClick(index){
      this.now = index;
    }
  },
  components: {
    jsInfo,
    exam
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.float-right{
  position: fixed;
  bottom:1rem;
  right:1rem;
  background: #3BC1BD;
  padding:0.5rem;
}
.float-right a{
  color:#FFF;
}
</style>



