<template>
  <div>
    <div class="nav-scroller bg-white box-shadow">
      <nav class="nav nav-underline">
        <a v-for="item,index in arr" class="nav-link active" :href="'#item'+index">{{item}}</a>
      </nav>
    </div>
    <!-- //获取行间样式 -->
    <getLineStyle></getLineStyle>
    <!--//单物体匀速运动  -->
    <sports></sports>
    <!-- //单物体缓冲运动 -->
    <bufferMotion></bufferMotion>
    <!-- //多物体运动 -->
    <sportsFrame></sportsFrame>
  </div>
</template>
<script>
import getLineStyle from '@/components/mode/getLineStyle'
import sports from '@/components/mode/sports'
import bufferMotion from '@/components/mode/bufferMotion'
import sportsFrame from '@/components/mode/sportsFrame'
export default {
  data () {
    return {
      arr: ['JS获取非行间样式','单物体运动框架(匀速/缓冲)','多物体运动框架']
    }
  },
  components:{
    getLineStyle,
    sports,
    bufferMotion,
    sportsFrame
  }
}
</script>
<style media="screen">

</style>



