<template>
  <div role="main" class="container" id="item1">
    <div class="d-flex align-items-center p-3 my-3 text-white-50 bg-green rounded box-shadow">
      <img class="mr-3" src="./logo.png" alt="" width="48" height="48">
      <div class="lh-100">
        <h6 class="mb-0 text-white lh-100">JS单物体匀速运动框架</h6>
        <small>Make by Becky</small>
      </div>
    </div>
    <div class="bs-docs-example">
      <input type="button" value="向左" v-on:click="startMove(0)"/>
      <input type="button" value="向右" v-on:click="startMove(500)"/>
      <div id="sport1" ref="sport1"></div>
      <div id="sport2" ref="sport2"></div>
    </div>
    <pre class="prettyprint pre-scrollable">
      //HTML
      &lt;input type="button" value="向左" id="btn"/&gt
      &lt;input type="button" value="向右" id="btn1"/&gt
      &lt;div id="box"&gt&lt;/div&gt
      &lt;div id="box2"&gt&lt;/div&gt
      //JS
      window.onload = function(){
        var box = document.getElementById("box");
        var left = document.getElementById("btn");
        var right = document.getElementById("btn1");
        right.onclick = function(){
          move(500);
        };
        left.onclick = function(){
          move(0);
        }
      };
      <code>
      var timer = null;
      function move(iTarget){
        var box = document.getElementById("box");
        clearInterval(timer);
        timer = setInterval(function(){
          var speed = 0;
          if(box.offsetLeft < iTarget){
            speed = 7;
          }else{
            speed = -7;
          }
          if(Math.abs(iTarget - box.offsetLeft) <= 7){
            clearInterval(timer);
            box.style.left = iTarget+'px';
          }else{
            box.style.left = box.offsetLeft+speed+'px';
          }
      },30);
      </code>
    </pre>
  </div>
</template>
<script type="text/javascript">
export default {
  data () {
    return {
    }
  },
  methods: {
    startMove:function(num){
      let sportBox1 = this.$refs.sport1;
      this.move(sportBox1,num)
    },
    move: function(obj,iTarget){
      let timer = null;

      clearInterval(timer);
      timer = setInterval(function(){
        var speed = 0;
        console.log(box)
        if(obj.offsetLeft < iTarget){
          speed = 7;
        }else{
          speed = -7;
        }

        if(Math.abs(iTarget - obj.offsetLeft) <= 7){
          clearInterval(timer);
          obj.style.left = iTarget+'px';
        }else{
          obj.style.left = obj.offsetLeft+speed+'px';
        }
      },30);
    }
  }
}
</script>
<style media="screen">
#sport1{
  width:150px;
  height:150px;
  background:#f1b8e4;
  position:absolute;
}
#sport2{
  width:1px;
  height:200px;
  background:#dcff93;
  margin-left:481px;
}
</style>



