<template>
<div class="container">
  <div class="row">
    <div class="col-sm-4 box" v-for="item in arr">
      <div class="card mb-4 box-shadow">
        <a :href="item.href" target="_blank">
            <img class="card-img-top" :src="item.img" alt="小例子">
            <div class="card-body">
              <h6 class="blockquote-footer">{{item.name}}</h6>
            </div>
        </a>
      </div>
    </div>
  </div>
</div>
</template>
<script>
export default {
  name: 'exam',
  data () {
    return {
      arr : [
        {name:'CSS3 3D 正方体旋转',img:'static/images/rect.png', href:"https://beckyisme.github.io/mine/rect3D.html"},
        {name:'CSS3 3D 翻书',img:'static/images/book.png',href:"https://beckyisme.github.io/mine/3d_book.html"},
        {name:'CSS3 3D 扑克牌翻转',img:'static/images/pu.png',href:"https://beckyisme.github.io/mine/3D_pu.html"},
        {name:'JS原生 分享弹窗',img:'static/images/share.png',href:"https://beckyisme.github.io/mine/share.html"},
        {name:'JS原生 透明度渐变',img:'static/images/opacity.png',href:"https://beckyisme.github.io/mine/ph_effect.html"},
        {name:'JS原生 小方块缓冲运动',img:'static/images/ice.png',href:"https://beckyisme.github.io/mine/ice.html"},
        {name:'JS原生 悬浮框（缓冲运动）',img:'static/images/ice1.png',href:"https://beckyisme.github.io/mine/ice1.html"},
        {name:'JS原生 选项卡',img:'static/images/tab.png',href:"https://beckyisme.github.io/mine/selectTab.html"},
        {name:'JS原生 简易日历',img:'static/images/simpleDate.png',href:"https://beckyisme.github.io/mine/simpleDate.html"},
        {name:'JS原生 多物体运动',img:'static/images/changew.png',href:"https://beckyisme.github.io/mine/changeWidth.html"},
        {name:'JS原生 多物体渐变',img:'static/images/changeo.png',href:"https://beckyisme.github.io/mine/changeOpacity.html"}
      ]
    }
  }
}
</script>
<style media="screen">
.container{
  margin-top: 20px;
}
.card a:hover{
  text-decoration: none;
  color: #33333;
}
</style>



