<template>
  <div role="main" class="container" id="item0">
    <div class="d-flex align-items-center p-3 my-3 text-white-50 bg-green rounded box-shadow">
      <img class="mr-3" src="./logo.png" alt="" width="48" height="48">
      <div class="lh-100">
        <h6 class="mb-0 text-white lh-100">获取非行间样式</h6>
        <small>Make by Becky</small>
      </div>
    </div>
    <div class="bs-docs-example">
        <div id="box" ref="box" style="width:100px;height:100px;border:1px solid #333;"></div>
        <input type="button" id="btn" value="获取宽" class="btn" v-on:click="getStyle('width')"/>
    </div>
    <pre class="prettyprint pre-scrollable">
      //HTML
      &lt;div id="box"&gt;&lt;/div&gt;
      &lt;input type="button" id="btn" value="获取宽" class="btn"/&gt;
      //JS
      var box = document.getElementById("box");
      var btn = document.getElementById("btn");
      <code>function getStyle(obj,value){
        if(obj.currentStyle){
          return obj.currentStyle[value];
        }else{
          return getComputedStyle(obj,false)[value];
        }
      }</code>
      btn.onclick = function(){
        alert(getStyle(box,'width'));
      }
    </pre>
  </div>
</template>
<script type="text/javascript">
export default {
  methods: {
    getStyle: function (value) {
      let obj = this.$refs.box;
      if (obj.currentStyle) {
        alert(obj.currentStyle[value])
      } else {
        alert(getComputedStyle(obj, false)[value])
      }
    }
  }
}
</script>
<style media="screen">

</style>



