<template>
  <div role="main" class="container" id="item2">
    <div class="d-flex align-items-center p-3 my-3 text-white-50 bg-green rounded box-shadow">
      <img class="mr-3" src="./logo.png" alt="" width="48" height="48">
      <div class="lh-100">
        <h6 class="mb-0 text-white lh-100">JS多物体运动框架</h6>
        <small>Make by Becky</small>
      </div>
    </div>
    <div class="bs-docs-example">
      <p><a href="https://beckyisme.github.io/mine/changeWidth.html" target="_blank">查看DEMO</a></p>
    </div>
    <pre class="prettyprint pre-scrollable">
      //多物体运动框架，除了颜色渐变
      <code>
      function move(obj,iTarget){
        clearInterval(obj.timer);
        obj.timer = setInterval(function(){
          var speed = (iTarget - obj.offsetWidth)/5;
          speed = speed > 0 ? Math.ceil(speed) : Math.floor(speed);
          if(obj.offsetWidth == iTarget){
            clearInterval(obj.timer);
          }else{
            obj.style.width  = obj.offsetWidth + speed + 'px';
          }
        },30);
      }
      </code>
      //多物体颜色渐变框架
      <code>
      function move(obj, iTarget){
        clearInterval(obj.timer);
        obj.timer = setInterval(function(){
          var speed = (iTarget - obj.level)/5;
          speed = speed > 0 ? Math.ceil(speed) : Math.floor(speed);
          if(obj.level == iTarget){
            clearInterval(obj.timer);
          }else{
            obj.level+=speed;
            obj.style.opacity = obj.level/100;
            obj.style.filter = 'alpha(opacity:'+obj.level+')';
          }
        },30);
      }
      </code>
    </pre>
  </div>
</template>
<script type="text/javascript">
export default {
  data () {
    return {
    }
  },
  methods: {
    startMove:function(num){
      let sportBox1 = this.$refs.sport1;
      this.move(sportBox1,num)
    },
    move: function(obj,iTarget){
      let timer = null;

      clearInterval(timer);
      timer = setInterval(function(){
        var speed = 0;
        console.log(box)
        if(obj.offsetLeft < iTarget){
          speed = 7;
        }else{
          speed = -7;
        }

        if(Math.abs(iTarget - obj.offsetLeft) <= 7){
          clearInterval(timer);
          obj.style.left = iTarget+'px';
        }else{
          obj.style.left = obj.offsetLeft+speed+'px';
        }
      },30);
    }
  }
}
</script>
<style media="screen">
#sport1{
  width:150px;
  height:150px;
  background:#f1b8e4;
  position:absolute;
}
#sport2{
  width:1px;
  height:200px;
  background:#dcff93;
  margin-left:481px;
}
</style>



